module.exports = "import React from \"react\";\nimport DayPicker from \"react-day-picker\";\n\nimport \"react-day-picker/lib/style.css\";\nimport \"../style/YearCalendarExample.scss\";\n\nexport default class Example extends React.Component {\n\n  static displayName = \"YearCalendarExample\";\n\n  state = {\n    year: (new Date()).getFullYear()\n  }\n\n  showPrevious() {\n    this.setState({\n      year: this.state.year - 1\n    });\n  }\n\n  showNext() {\n    this.setState({\n      year: this.state.year + 1\n    });\n  }\n\n  render() {\n    const { year } = this.state;\n    return (\n      <div className=\"YearCalendarExample\">\n        <h1>\n          <a onClick={ this.showPrevious.bind(this) }>{ year - 1 }</a>\n          { year }\n          <a onClick={ this.showNext.bind(this) }>{ year + 1 }</a>\n        </h1>\n        <DayPicker\n          canChangeMonth={ false }\n          initialMonth={ new Date(year, 0, 1) }\n          numberOfMonths={ 12 }\n        />\n      </div>\n    );\n  }\n\n}\n"


